// Blog home


.card.blog-item{
    border:0px;

    .card-footer{
        background: transparent;
    }

    .read-more{
        font-size: 14px;
        color: $primary-color;
        font-weight: 600;
    }

    .section-subtitle{
        color: $black;
    }
}


.blog-item{
    box-shadow: 12px 7px 90px 0 rgba(110, 123, 131, 0.1);
    background: $light;
    border-radius:3px;


    .blog-item-content{
        padding: 30px;
        border-top:5px solid transparent;
	    -webkit-transition: all 600ms;
	       -moz-transition: all 600ms;
	        -ms-transition: all 600ms;
	         -o-transition: all 600ms;
	            transition: all 600ms;
    }

    &:hover .blog-item-content{
		border-top:5px solid $primary-color;
		cursor: pointer;
    }
    &:hover h4>a{
    	color: $primary-color;
    }

    .card-footer{
        padding:10px  30px;
    }
}

.text-color{
    color: $primary-color;
}


//  Blog Page
//  
.post-navigation{
    a{
        width: 50px;
        height:50px;
        border:2px solid rgba(0,0,0,0.08);
        display: inline-block;
        text-align: center;
        font-weight: 700;
        padding-top: 10px;

        &:hover{
            background: $secondary-color;
            color: $light;
            border-color: $secondary-color;
        }

        &.active{
            background: $secondary-color;
            color: $light;
            border-color: $secondary-color;
        }
    }


}


.sidebar-widget{
    background:#F4F7FC;
    padding: 30px;

    h5{
        position: relative;
        padding-bottom: 15px;

        &:before{
            position: absolute;
            content:"";
            left: 0px;
            bottom:0px;
            width: 40px;
            height:2px;
            background: $primary-color;
        }
    }
    

    &.category{
        ul li{
            padding: 10px 0px;
            margin-bottom: 5px;
            border-bottom:1px solid rgba(0,0,0,0.05);

            &:last-child{
                border-bottom:0px;
            }
        }
    }

    &.tags{
        a{
            display: inline-block;
            padding: 5px 12px;
            text-transform: capitalize;
            margin-bottom: 4px;
            background: $light;
        }
    }
}


blockquote{
    font-size: 20px;
    padding: 40px;
    border-left:3px solid $primary-color;
    background: #eee;
}

.single-tags{
    li{
        a{
            display: inline-block;
            padding: 5px 14px;
            text-transform: capitalize;
            margin-bottom: 4px;
            border:1px solid rgba(0,0,0,0.05);
        }
     }
}