.service-block{
	border:1px solid rgba(0,0,0,0.04);
	padding:40px 20px;
	position: relative;
	.content{
		margin-top: 20px;
	}

	i{
		color: $primary-color;
	}

	&:before{
		background: $primary-color;
		position: absolute;
		content: "";
		left: 0px;
		top:0px;
		bottom:0px;
		width: 0;
		height: 100%;
		z-index: -1;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	&:hover:before{
		width: 100%;
	}

	h4,p,i{
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	&:hover h4,
	&:hover p,
	&:hover i{
		color: $light!important;
	}
}


//  Service home
//  


.service-2{
    position: relative;
    background: url("/images/bg/about-shape.png")no-repeat;
    background-position: center bottom;
    -webkit-background-size: cover;
    background-size: cover;

    .section-heading{
    	margin-bottom: 50px;
    }
}

.service-item-block{
	padding: 30px;
	transition: all .5s cubic-bezier(.77,0,.175,1);
	margin-bottom: 15px;
	border:1px solid rgba(0,0,0,0.04);
	background: $light;

	i{
		color: $primary-color;
		margin-bottom: 10px;
	}

	&:hover{
		box-shadow: 12px 7px 90px 0 rgba(110,123,131,.1);
	}
}

.service-img{
	margin-top: -20px;
}

.primary-bg{
	background: $primary-color;
}
.secondary-bg{
	background: $secondary-color;
}