
/*----------------------
 BANNER STYLE
-----------------------*/

.py-7 {
    padding:4.5rem 0;
}

.py-8 {
    padding: 8.5rem 0;
}



/*-------------------------
 HOME -2 
-----------------------------*/
.banner{
    background: #F8FAFF;
    padding-bottom: 130px;
}


.main-banner {
    padding: 100px 0px;
    p{
        margin-bottom: 40px;
    }
    .display-4{
        margin-top: 45px;
        font-weight: 700;
    }
}


.banner-img{
    padding-top: 70px;
}



@include mobile{
    .display-4 {
        font-size: 2.5rem;
    }
}

@include mobile-xs{
    .display-4{
        font-size: 30px;
    }
}

@include tablet{
    .display-4 {
        font-size: 2.5rem;
    }
}
