/*------------------------------------
SECTION TESTIMONIAL
-------------------------------------*/

.test-item{
    padding: 40px;
    position: relative;
    background: $light;
     -webkit-transition: all .7s ease;
    -o-transition:all .7s ease ;
    transition:all .7s ease ;
    margin: 0px 5px;
    margin-bottom: 40px;
    border-radius: 15px;
    border:1px solid rgba(0,0,0,0.04);

    &:before{
        position: absolute;
        content:"";
        width: 80%;
        height:90%;
        background: $light;
        bottom:-30px;
        border-radius: 15px;
        margin: 0 auto;
        z-index: -1;
    }

    i{
        font-size: 60px;
        opacity: .6;
        position: absolute;
        right: 40px;
        bottom: 20px;
        color: $primary-color;
    }

    &:hover{
        cursor: pointer;
    }
}


.test-author-thumb{
    .img-block{
        margin-top: -8px;
    }
    img{
        border-radius:100%;
        width: 80px;
        height:80px;
        border:5px solid $border-color;
    }
}

.testimonial-item-content{
    margin-top: 20px;
}




/*----------------------
 TETSIMONIAL-2
-------------------------*/

.testimonial .content img{
    margin: 0 auto 30px;
    border: 3px solid $light;
}

.img-md{
    width: 120px;
    height: 120px;
    border-radius: 100%;
}

//  Tetsimnial-2
//  

.test-block{
    background: $light;
    margin: 0px 1px;
    border-radius: 4px;
    border:1px solid rgba(0,0,0,0.03);
    padding: 40px;
    h2{
        text-transform: capitalize;
        color: $primary-color;
    }
}