
/*-------------------
   MENU
----------------------*/


.header-padding{
    padding: 0px 130px;
}
.main-navigation{
    z-index: 9999;

    border-bottom:1px solid rgba(0,0,0,0.05);

    .btn{
        padding: .65rem 2rem;
        background: transparent;

        &:hover{
            background: $secondary-color;
        }
    }
}

.nav-item{
    padding: 12px 6px;
}
.navbar-nav .nav-link {
    padding-right: 1rem !important;
    padding-left: 1rem !important;
    font-weight: 700;
    font-size: 16px;
    text-transform:capitalize;
}
.nav-item.active{
    position: relative;
    &:before {
        position: absolute;
        content: "";
        left: 0px;
        bottom: -10px;
        width: 30px;
        height: 2px;
        background:$primary-color;
        right: 0px;
        margin: 0 auto;

    }

    .nav-link{
        color: $primary-color;
    }
}

.navbar {
    position: relative;
}

.fixed-top {
    position: fixed;
    z-index: 1030;
    right: 0;
    left: 0;
}





.navbar-toggler-icon {
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    content: "";
    vertical-align: middle;
    background: no-repeat 50%;
    background-size: 100% 100%
}



.nav-item.dropdown {
    .dropdown-menu{
        -webkit-transition: all 300ms ease;
        -o-transition:all 300ms ease;
        transition: all 300ms ease;
        display: block;
        visibility: hidden;
        opacity: 0;
        top: 120%;
        -webkit-box-shadow: 2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
        box-shadow: 2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
        border: 0px;
        padding: 0px;
        position: absolute;
    }
    &:hover .dropdown-menu{
        opacity: 1;
        visibility: visible;
        top: 100%;
    }
}


.header-bar.fixed-header .main-navigation {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background: rgba(20,27,40,.90);
    border-color: transparent;

}


/*-------------------
  DROPDOWN MENU
----------------------*/


.dropdown-menu {
    opacity: 0
}

.dropdown-menu.show {
    animation: .2s forwards b;
}

@keyframes b {
    to {
        opacity: 1
    }
}

.dropdown-item {
    font-size: 15px;
    font-weight: 500;
    padding: 12px 20px;
    margin: 0 auto;
    transition: background-color .1s;
    text-transform:capitalize;

    &:hover {
        color:$light;
        text-decoration: none;
        background-color: $primary-color;

    }
}




.navbar-collapse.collapse.show{
    background: $light;
}


@include mobile{
    .nav-item.dropdown .dropdown-menu {
        margin: 0 auto;
        left: 0px;
        right: 0px;
        text-align: center;
    }
}

@include mobile-xs{
    .nav-item.dropdown .dropdown-menu {
        margin: 0 auto;
        left: 0px;
        right: 0px;
        text-align: center;
    }
}

@include tablet{
    .nav-item.dropdown .dropdown-menu {
        margin: 0 auto;
        left: 0px;
        right: 0px;
        text-align: center;
    }
}


.header-white{
    background: $light;
    z-index: 999999;
}