
.section-subtitle{
	font-size: 14px;
	padding: 0.5rem 1rem;
	color: $black;
	border-radius: 25px;
	background-color: rgba(121, 110, 255, 0.1);
}




.about-block{
	.process{
		margin-right: 30px;
	}
	.number{
		width: 50px;
		height: 50px;
		background: $primary-color;
		color: $light;
		border-radius: 100%;
		display: inline-block;
		text-align: center;
		font-weight: 600;
		padding-top: 10px;
	}

}


.text-lg{
	font-size: 40px;
	line-height: 1.1;
}



// Feature section


.feature{
	margin-top: -70px;
	position: relative;
	z-index: 9999;
}


.feature-item{
	box-shadow: 12px 7px 90px 0 rgba(110,123,131,.1);
	background: $light;
	border-radius: 5px;
	position: relative;
	z-index: 1;

	p{
		margin-bottom: 0px;
	}
	h4{
		position: relative;
		&:before {
		    position: absolute;
		    content: "";
		    left: 0px;
		    top: 47px;
		    background: $primary-color;
		    width: 30px;
		    height: 2px;
		    -webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
		}
	}

	&:before{
		background: $primary-color;
		position: absolute;
		content: "";
		left: 0px;
		top:0px;
		bottom:0px;
		width: 0;
		height: 100%;
		z-index: -1;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	&:hover:before{
		width: 100%;
	}

	h4,p,i{
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	&:hover h4,
	&:hover p,
	&:hover i{
		color: $light!important;
	}

	&:hover h4:before{
		background: rgba(255,255,255,.5);
	}


}


.video-section{
	position: relative;

	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height:100%;
		background:rgba(0,0,0,0.7);
		opacity: .8;

	}

	.video-play{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		right:0px;
		margin:auto;
		bottom:0px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;

		i{
			font-size: 40px;
			width: 120px;
			height: 120px;
			background:$secondary-color;
			color:  $light;
			border-radius: 100%;
			display: inline-block;
			text-align: center;
			padding-top: 40px;
			
		}
	}
}







//  CTA
.cta-2{
	background: url("../images/banner/service.jpg")no-repeat 50% 50%;
	background-size: cover;
}

.cta-content{
	background: $primary-color;
	padding: 65px;

	h2,p{
		color: $light;
	}

	p{
		margin-bottom: 35px;
	}
}



// CTA


.cta{
	position: relative;
	 background: url("../images/banner/service.jpg")no-repeat;
    -webkit-background-size: cover;
    background-size: cover;

   &:before{
   	background:$primary-color;
   	opacity: .9;
    position: absolute;
    content: "";
    left: 0px;
    top:0px;
    width: 100%;
    height:100%;
   }

}


// Counter ABout page section

.counter-block{
	span{
		color: $primary-color;
		font-size: 70px;
		line-height: 1.4;
	}
}


// page-break-inside:  banner 
// 
.page-banner-area{
	background: #F8FAFF;
}


@include mobile{
	.text-lg {
	    font-size: 30px;
	    line-height: 1.3;
	}
}
@include mobile-xs{
	.feature-item h4:before{
		display: none;
	}

	.service-item-block {
		display: block!important;

		.icon{
			margin-bottom: 20px;
		}
	}

	.about-block {
		display: block!important;

		.process{
			margin-bottom: 20px;
		}
	}

	.test-author-thumb{
		display: block!important;

		.img-block{
			margin-bottom: 10px;
		}
	}

	.cta-content {
	    padding: 30px;
	}

	.footer-socials a {
	    margin-bottom: 5px;
	}

	.footer-btm-menu a {
	    margin-left: 0px;
	    margin-left: 25px;
	}

	.pricing-box{
		padding: 20px 20px!important;
		margin-bottom: 30px!important;
	}

}


@include tablet{
	.test-author-thumb{
		display: block!important;

		.img-block{
			margin-bottom: 10px;
		}
	}

}